import React from "react";
import styles from "./item.module.css";
import { Link } from "@reach/router";

export default ({
  listPicUrl,
  simpleDesc,
  name,
  counterPrice,
  colorNum,
  wait,
  id
}) => (
  <li className={styles.overallWrapper}>
   <Link to={`/test/detail/item/${id}`}>
      <div className={styles.photoWrapper}>
        <img src={wait ? "" : listPicUrl} className={styles.photo} alt="" />{" "}
        <div className={styles.advantage}>{simpleDesc}</div>
        {colorNum ? (
          <div className={styles.colorNum}>
            <div>{colorNum}</div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        ) : (
          ""
        )}
      </div>
      <div className={styles.name}>{name}</div>
      <div className={styles.money}>{counterPrice}</div>
    </Link>
  </li>
);



