import React from "react";
import styles from "./itemList.module.css";
import Item from "./item/item";
import Observable from "../../overallModel/OverallMoveCache";

export default class MoveItemList extends React.Component {
  constructor() {
    super(...arguments);
    this.state = {
      ...this.props,
      wait: true
    };
    this.getElement = this.getElement.bind(this);
    this.getObservable = Observable();
    this.outState = this.getObservable.outState;
    this.putState = this.getObservable.putState;
  }
  getElement(element) {
    if (element === null) {
      return false;
    }
    this.putState.next({
      type: "getCacheList",
      element: element,
      state: this.state
    });
  }
  componentWillUnmount() {
    this.putState.unsubscribe();
    this.outState.unsubscribe();
  }
  componentDidMount() {
    this.outState = this.outState.subscribe(value => this.setState(value));
  }
  render() {
    const {
      category: { name, frontName },
      itemList,
      wait
    } = this.state;
    return (
      <div className={styles.overallWrapped} ref={this.getElement}>
        <div className={styles.header}>
          {name}
          <div className={styles.desc}> {frontName}</div>
        </div>
        <ol className={styles.list}>
          {itemList.map(value => (
            <React.Fragment key={value.id}>
              <Item {...value} wait={wait} />
            </React.Fragment>
          ))}
        </ol>
      </div>
    );
  }
}



